import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhImage, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  public foodList: any[] = [];
  public searchList: any[] = [];
  public finalList: any[] = [];
  public componentFoods: any[] = [];
  comboForm: FormGroup;
  show: any = []
  value: any = 0;
  total: number = 0;
  public price: any;
  barcode: boolean = true;
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  editPrice: any = {}
  createProd: boolean = false;
  heightScroll: string;
  list_category = this.vhQueryCafe.getlocalCategorys().filter(item => item.type == 5)
  public indexTab = 0;
  public tabs: any = [
    this.languageService.translate('All'),
    'Product',
    'Service',
    'Ingredient',
    'Topping',
  ];
  constructor(
    private router: Router,
    private vhImage: VhImage,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    this.initForm()
    this.getFoodLList()

  }
  ngOnDestroy() {
    if (!this.createProd && this.comboForm.value['img'])
      this.vhImage.deleteThumbnail([this.comboForm.value['img']]).then(() => { })
  }
  ngAfterViewChecked() {
    if (document.querySelector(".combo-table"))
      this.heightScroll = document.querySelector(".combo-table").clientHeight - document.querySelector(".ant-table-thead").clientHeight + "px";

    this.cdRef.detectChanges();
  }

  /**
   * khi to form vi cc trng  to combo ln DB
   * @example this.initForm()
   */
  initForm() {
    this.comboForm = new FormGroup({
      name: new FormControl("", [Validators.required]),
      img: new FormControl(""),
      barcode: new FormControl(''),
      // allow_sell: new FormControl(true),
      delete_hidden: new FormControl(false),
      describe: new FormControl(''),
      price: new FormControl(0),
      time_warning : new FormControl(0),
      unit: new FormControl('', [Validators.required]),
      id_category: new FormControl('', [Validators.required]),
    })
  }

  // set barcode t ng
  setBarcode() {
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10)
    }
    this.checkBarcode(newbarcode);
    if (this.barcode) {
      this.comboForm.controls['barcode'].setValue(newbarcode)
    }
  }

  /**
   * kim tra barcode  tn ti hay cha
   * @example this.checkBarcode('123456')
   */
  checkBarcode(barcode: string) {
    let detailproduct = this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode)
    if (detailproduct == null) {
      this.barcode = true;
    } else {
      this.barcode = false
    }
  }
  // modal component dishes/beverage
  isVisibleAddComponent = false;
  showComponent() {
    this.indexTab = 0
    this.isVisibleAddComponent = true
  }

  handleCancelComponent() {
    this.isVisibleAddComponent = false;
  }


  /**
   * get danh sch sp v dch v  chn vo thnh phn cho combo
   * @example this.getFoodLList()
   */
  getFoodLList() {
    this.foodList = this.vhAlgorithm.sortDatebyASC(this.vhQueryCafe.getlocalDetailProducts().filter(filter => filter.type != 5).map((localProduct) => {
      if (localProduct.units) {
        localProduct.units = this.vhAlgorithm.sortNumberbyASC(
          localProduct.units,
          'ratio'
        );
        let unit = this.vhQueryCafe.getUnit_byRatio(localProduct.units, 1);
        return {
          ...localProduct,
          choose: false,
          display: 'flex',
          quantity: 1,
          ...unit,
        };
      } else
        return { ...localProduct, choose: false, display: 'flex', quantity: 1 };
    }), 'name');
    this.searchList = this.foodList;
    console.log(this.foodList);
    
    this.finalList = this.searchList
  }

  tempVal: string = ''
  /**
   * lc tn sp theo tn
   * @example this.searchFood('')
   */
  searchFood(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length) this.finalList = this.vhAlgorithm.searchList(tempVal, this.searchList, ['name'])
    else this.finalList = this.searchList
  }
  /**
   * bt sk thay i tab xem loi sn phm
   * @param event 
   * @example this.changeTypeGood(0)
   */
  changeTypeGood(event) {
    this.indexTab = event;
    if(this.indexTab == 0 ) this.finalList = this.foodList;
    else this.finalList = this.foodList.filter(filter => filter.type == this.indexTab)
    this.searchList = this.foodList;
  }


  /**
   * bt sk nhn nt ok ca modal chn sp vo combo, x l cc sp  chn vo thnh phn ca combo
   * @example this.handleComponent()
   */
  handleComponent() {
    this.componentFoods = this.searchList.filter(item => item.choose).map(e => {return {...e, quantity :1 ,price_origin  : e.price}});
    this.show = this.componentFoods
    setTimeout(() => this.renderPrice(this.componentFoods), 100)
    this.comboPrice()
    this.isVisibleAddComponent = false
  }

  /**
   * cleavejs cho cc sp trong combo  x l gi sp
   * @example this.renderPrice([])
   */
  renderPrice(arr: any[]) {
    arr.forEach(item => {
      if (!item.id_subproduct) this.editPrice['product-' + item._id] = this.vhAlgorithm.vhnumeral('.product-' + item._id)
      else this.editPrice['product-' + item.id_subproduct] = this.vhAlgorithm.vhnumeral('.product-' + item.id_subproduct)
    })
  }

  /**
   * tnh li gi tng combo
   * @example this.comboPrice()
   */
  comboPrice(dish?: any) {
    if (dish) {
      let price: string = '';
      if (dish.id_subproduct) { price = this.editPrice['product-' + dish.id_subproduct].getRawValue(); }
      else { price = this.editPrice['product-' + dish._id].getRawValue(); }
      dish.price = parseFloat(price)
    }
    this.total = this.show.reduce((prev: number, next) => prev + next.quantity * next.price, 0);
    this.comboForm.controls['price'].setValue(this.total);
  }

  // Thc hin xa sn phm trong combo
  deleteFood(food) {
    this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate('Are you sure to delete this product?')}`, "OK", this.languageService.translate('Cancel')).then(() => {
      this.componentFoods.splice(this.componentFoods.findIndex(item => item.name == food.name), 1);
      this.finalList.map(item => {
        if (item.name == food.name) {
          item.display = 'flex';
          item.price = food.price_origin;
          item.choose = false
        }
      })
      this.show = this.componentFoods;
      this.comboPrice()
      this.editPrice['product-' + food._id] = null
      Promise.all([this.vhComponent.showLoading("", "transparent-loading", null, null, false),])
        .finally(() => this.vhComponent.hideLoading(0))
    }, () => { })
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/dishes/combo'], { state: this.dataRestore });
  }

  /**
   * to combo ln DB
   * @example this.saveCombo()
   */
  saveCombo() {
    let update = this.cleanForm(this.comboForm).value;
    update['units'] = [{
      barcode: update.barcode,
      price: update.price,
      unit: update.unit,
      ratio: 1,
      default: true
    }]
    delete update.barcode;
    delete update.price;
    delete update.unit;
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      if (!this.barcode) {
        this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(1500, this.languageService.translate("Barcode already exists"), "alert-toast"))
      } else {
        this.vhQueryCafe.addProduct_Cafe({ ...update, type: 5 }).then(async ({ _id, name }: any) => {
          console.log(_id);
          
          this.createProd = true
          if (this.show.length) {
            await this.addComponentFoods(_id, this.show)
          }
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(1000, name + " " + (this.languageService.translate("has been added successfully")), "success-toast");
            this.goBack();
          })
        }).catch(err => {
          this.vhComponent.hideLoading(0).then(() => this.vhComponent.showToast(1500, this.languageService.translate("An error occurred while adding product. Please try again"), "alert-toast"))
        })
      }
    })
  }
  /**
   * bt sk thay i n v ca sp trong combo
   * @param data sp trong combo
   * @param event gi tr ratio ca n v
   * @example this.changeRatio(combo_prodct, 1)
   */
  changeRatio(data, event){
    let unit = this.vhQueryCafe.getUnit_byRatio(data.units, event);
    data.price = unit.price;
    data.ratio = event;
    setTimeout(() => {
      this.comboPrice(data)
    }, 100);
  }

  // Hm thc hin xa space (TRIM) cc control trong form
  public cleanForm(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((key) => {
      if (key === 'name' || key === 'barcode' || key === 'brand' || key === 'unit') {
        if (formGroup.get(key).value) {
          let value = formGroup.get(key).value.toString().trim();
          formGroup.get(key).setValue(value)
        }
      }
    });
    return formGroup;
  }

  /**
   * thm danh sch thnh phn combo ln DB
   * @example this.addComponentFoods(id_combo, [])
   */
  addComponentFoods(_id, foods: any[]) {
    return new Promise<any>((resolve) => {
      this.vhQueryCafe.addCombosProduct_Cafe(_id, foods.map(food => {
        let data = {
          id_product: food._id,
          type: food.type,
          quantity: food.quantity,
          price: food.price,
          ratio: food.ratio
        }
        if (food.id_subproduct) data['id_subproduct'] = food.id_subproduct;
        return data;
      })).then((combos) => {
        if (combos) resolve(true)
      }, error => {
        console.log('error', error);
        resolve(false)
      }).catch(e => {
        console.log(e);
        
      })
    })
  }

  getFile() {
    document.getElementById("file-upload").click();
  }

  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 128, 128, "products", this.comboForm.value.img || null).then(
      photoURL => {
        this.comboForm.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), "success-toast");
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), "alert-toast");
      }
    );
  }
      
      
      
  /** Nu l true th l ang thm danh mc, dng  hin th trng thi loading, trnh b nhn nhiu ln */
  isAddingCategory: boolean = false;
  /** Input nhp tn danh mc */
  @ViewChild('categoryName') categoryName;

  /**
   * Thm danh mc
   */
  addCategory(categoryName: any) {
    const value = categoryName.value ? categoryName.value?.toString()?.trim() : '';
    if (value) {
      // Kim tra xem danh mc  tn ti hay cha
      let index = this.vhQueryCafe.getlocalCategorys()
                    .filter(res => res.type == VhType.COMBO)
                    .findIndex(item => this.vhAlgorithm.changeAlias(item.name).toLowerCase() == this.vhAlgorithm.changeAlias(value).toLowerCase());

      //  tn ti
      if (index !== -1) {
        this.vhComponent.showToast(1500, this.languageService.translate("Category existed"), 'alert-toast');
        return;
      }

      // Cha tn ti th thm
      this.isAddingCategory = true;
      this.vhQueryCafe.addCategory({ name: value, type: VhType.COMBO }).then((res: any) => {
        this.categoryName.nativeElement.value = ''; // Reset input
        this.list_category.unshift(res); // Thm vo u danh sch
        this.comboForm.controls['id_category'].setValue(res._id); // Chn ci mi thm
        this.vhComponent.showToast(2000, (this.languageService.translate("Category has been added successfully")), 'success-toast');
      }, err => {
        console.log(err);
        this.vhComponent.showToast(1500, this.languageService.translate("An eror occured while adding category. Please try again"), 'alert-toast');
      }).finally(() => {
        this.isAddingCategory = false;
      });
    }
    else {
      this.vhComponent.showToast(1500, this.languageService.translate('Category name is required'), 'alert-toast');
    }
  }
  
}
