import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-combo',
  templateUrl: './combo.component.html',
  styleUrls: ['./combo.component.scss']
})
export class ComboComponent {

  list_show_combo_hidden: any = [];
  list_show: any = [];
  list_subcombos: any = [];
  keySearch: any = '';
  selectedValue: any = '1'; //theo menu
  selectedIndex: number = 0; // theo index ca tab
  heightScroll: string;
  listTabs = ['Allow sale', 'Hidden'];
  dataRestore: any = this.router.getCurrentNavigation().extras.state;



  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    public languageService: LanguageService,
    private cdRef: ChangeDetectorRef,
    private vhComponent: VhComponent
  ) { }



  ngOnInit() {
    this.getData();
  }



  ngAfterViewChecked() {
    if (document.querySelector("#combo-list") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".combo-list-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#combo-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".combo-list-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }



  /**get data product, category */
  getData() {
    if (this.dataRestore) {
      this.keySearch = this.dataRestore.keySearch;
      this.selectedValue = this.dataRestore.selectedValue;
      this.selectedIndex = this.dataRestore.selectedIndex;
    }
    this.changeTab(this.selectedIndex);
  }


  list_combo_hidden = []
  /**
   * Ly danh sch sn phm  n
   */
  getHiddenProducts() {
    this.vhQueryCafe.refreshLocalProducts_Delete_Hidden(VhType.COMBO).then(() => {
      this.list_show_combo_hidden = this.vhQueryCafe.getlocalProducts(VhType.COMBO).filter(item => item.delete_hidden).map(item => this.processCombo(item));
      this.list_combo_hidden = this.list_show_combo_hidden
      this.list_subcombos = this.vhAlgorithm.sortStringbyASC(this.list_show_combo_hidden, 'name');
      this.searchCombos(this.keySearch);

    });
  }



  /**
   * X l d liu ca sn phm (units, subproduct, name_category, ...)
   * @param item
   * @returns
   */
  processCombo(item: any): any {
    return {
      ...item,
      quantity: item.combos.length,
      price: item.units[0].price,
    }
  }


  /**
   * cp nht sp theo trng truyn vo
   * @example this.updateProductByField({name: ''})
   */
  updateProductByField(id, value, parent?, is_sub?) {
    console.log('value', value);
    console.log('id', id);


    this.vhQueryCafe.updateProduct(id, value).then((res) => {
      console.log(res);
      this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Update successfully"), 2000);
      if (!value.delete_hidden) {
        this.list_combo_hidden = this.list_combo_hidden.filter(item => item._id != id);
        this.list_show_combo_hidden = this.list_show_combo_hidden.filter(item => item._id != id);
      }
    }, error => {
      console.log(error);
    }).catch(e => {
      console.log(e);
    })


  }

  /**
   * Khi thay i tab
   * @example this.changeTab('1');
   */
  changeTab(value) {
    this.filterByAllowSell(value);
    if (value == '1') {
      this.getHiddenProducts();
    }
  }



  /**
   * Lc danh sch sn phm theo allow_sell
   */
  filterByAllowSell(index) {
    this.list_show = this.vhQueryCafe.getlocalProducts(VhType.COMBO).map(item => this.processCombo(item));
    if (this.list_show) {
      if (index == 0) this.list_show = this.list_show.filter(item => item.delete_hidden == false);
      // else if(index == 1) this.list_show = this.list_show_combo_hidden.filter(item => !item.delete_hidden == false);
      this.list_subcombos = this.list_show;
      this.searchCombos(this.keySearch);
    }
  }



  /**
   * Tm kim sn phm (tab  n) theo tn
   * @example this.searchCombos('');
   */
  searchCombos(value) {
    this.list_show = this.vhAlgorithm.searchList(
      this.vhAlgorithm.changeAlias(value.toLowerCase()),
      this.list_subcombos,
      ['name']
    );
  }



  goBack() {
    this.router.navigate(['/cafe/dashboard/management/dishes']);
  }


  gotoAdd() {
    let dataRestore = { keySearch: this.keySearch, selectedValue: this.selectedValue, selectedIndex: this.selectedIndex }
    this.router.navigate(['/cafe/dashboard/management/dishes/combo/add'], { state: { dataRestore } });
  }


  gotoComboDetail(data, isFromHidden?: boolean) {
    let dataRestore = { keySearch: this.keySearch, selectedValue: this.selectedValue, selectedIndex: this.selectedIndex };
    const state = {
      data: data,
      dataRestore
    }
    if (isFromHidden) {
      state['isFromHidden'] = true;
    }
    this.router.navigate(['/cafe/dashboard/management/dishes/combo/detail'], {
      state: state
    });
  }



  /** hm sort cho cc ct */
  name_col = false;             // chn sort ct tn sn phm
  quantity_col = false;         // Chn sp xp theo n v
  price_col = false;            // Chn sp xp theo gi

  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   * @param typeList      // Bin dng  xc nh danh sch sp xp l cho bn hay khng cho bn
   */
  sortTable(colName) {
    let methodName = colName === 'name' ? 'sortVietnameseby' : 'sortNumberby';
    let direction = this[colName + '_col'] ? 'ASC' : 'DESC';
    this.list_show = this.vhAlgorithm[methodName + direction]([...this.list_show], colName);
  }

}
