import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import Cleave from 'cleave.js';
import { VhAlgorithm, VhImage, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  barcode: boolean = true;
  comboForm: FormGroup;
  public price: any;
  data: any;
   
  priceDef: any;
  editPrice: any[] = [];
  heightScroll: string;
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  list_category = this.vhQueryCafe.getlocalCategorys().filter(item => item.type == 5)
  constructor(
    private router: Router,
    private vhImage: VhImage,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef

  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data; 
    
  }

  ngOnInit(): void {
    this.initForm()

    if (this.data.combos) {
      for (let i = 0; i < this.data.combos.length; i++) {
        // let dataPush = {};
        let product;
        if(this.data.combos[i].id_subproduct){
          product = this.vhQueryCafe.getlocalDetailProduct(this.data.combos[i].id_subproduct)
        }else{
          product = this.vhQueryCafe.getlocalDetailProduct(this.data.combos[i].id_product)
        }
        this.data.combos[i].units = product.units;
      }
      setTimeout(() => {
        this.renderPrice(this.data.combos)
      }, 100)
    }
  }


  ngAfterViewChecked() {
    if (document.querySelector(".combo-table"))
      this.heightScroll = document.querySelector(".combo-table").clientHeight - document.querySelector(".ant-table-thead").clientHeight + "px";
    this.cdRef.detectChanges();
  }


  // nh dng hin th kiu gi ngoi giao din 100,000 VN
  renderPrice(arr: any[]) {
    arr.forEach((item) => {
      if (!item.id_subproduct) this.editPrice['product-' + item._id] = this.vhAlgorithm.vhnumeral('.product-' + item._id)
      else this.editPrice['product-' + item.id_subproduct] = this.vhAlgorithm.vhnumeral('.product-' + item.id_subproduct)
    })
  }

  // cp nht li gi tin khi i n v
  changeUnit(event, data) {
    let unit_exchange = data.units.find(item => item.ratio == event);
    data.price = unit_exchange.price;
    data.ratio = unit_exchange.ratio;
    data.unit = unit_exchange.unit;
    setTimeout(() => {
      this.comboPrice(data)
    }, 100);
  }

  /**
   * gn gi tr combo nhn dc t state truyn qua vo form  show ln giao din
   * @example this.initForm()
   */
  initForm() {
    this.comboForm = new FormGroup({
      name: new FormControl(this.data.name, [Validators.required]),
      img: new FormControl(this.data.img),
      allow_sell: new FormControl(this.data.delete_hidden ? false : true),
      delete_hidden: new FormControl(this.data.delete_hidden),
      price: new FormControl(this.data.units[0].price),
      barcode: new FormControl(this.data.units[0].barcode),
      describe: new FormControl(this.data.describe),
      unit: new FormControl(this.data.units[0].unit),
      time_warning : new FormControl(this.data.time_warning),
      id_category: new FormControl(this.data.id_category, Validators.required),
    })
  }


  // T ng to barcode v lu
  setBarcode(){
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10)
    }
    this.checkBarcode(newbarcode);
    if(this.barcode){
      this.comboForm.controls['barcode'].setValue(newbarcode)
      this.updateCombo({ units :[{ 
        barcode: this.comboForm.value['barcode'],
        price : this.comboForm.value['price'],
        unit : this.comboForm.value['unit'],
        ratio : 1
      }]})
    }
  }

  // Kim tra m barcode va to  tn ti cha ( cha: true,  tn ti: false)
  checkBarcode(barcode: string) {
    let detailproduct = this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(barcode)
    if(detailproduct == null){
      this.barcode = true;
    }else{
      this.barcode = false
    }
  }

  // Cp nht combo
  updateCombo(object) {
    if (object.hasOwnProperty('allow_sell')) {
      object = { delete_hidden: !object.allow_sell }
    }
    if(this.comboForm.valid)
      this.vhQueryCafe.updateProduct_Cafe(this.data._id, object)
  }


  //  hm Cp nht gi tng ca combo
  comboPrice(dish?: any) {
    console.log(dish);
    
    if (dish) {
      let price: string = '';
      if (dish.id_subproduct) { price = this.editPrice['product-' + dish.id_subproduct].getRawValue(); }
      else { price = this.editPrice['product-' + dish._id].getRawValue(); }
      dish.price = price ? parseFloat(price) : dish.price_origin
    }
    let price = 0
    for (let product of this.data.combos) {
      price = price + (product.price * product.quantity)
    }
    this.comboForm.controls['price'].setValue(price)
    this.updateCombo({ units :[{ 
      barcode: this.comboForm.value['barcode'],
      price : price,
      unit : this.comboForm.value['unit'],
      ratio : 1
    }]})
    if(dish) this.updateshow(this.data._id, dish)

  }

  // hm cp nht gi 1 sn phm khi thay i trong combo
  updatePrice(data){
    let dataUpdate = this.data.combos.find(item => item.id_product == data.id_product);
    dataUpdate.price = data.price;
    dataUpdate.ratio = data.ratio;
    dataUpdate.unit = data.unit;
    this.updateshow(this.data._id, dataUpdate);
    this.comboPrice(data);
  }


  /** ------ Modal Zorro---- */
  isVisible = false;
   /**
   * m modal xc nhn xa combo
   * @example this.showModal
   */
   showModal(): void {
    this.isVisible = true;
  }
  /**
   * bt sk nhn nt ok modal xc nhn xa combo
   * @example this.handleOk
   */
  handleOk(): void {
    this.isVisible = false;
    this.deleteCombo();
  }
  /**
   * ng modal xc nhn xa combo
   * @example this.handleCancel
   */
  handleCancel(): void {
    this.isVisible = false;
  }

  /**
   * xa combo khi DB
   * @example this.deleteCombo();
   */
  deleteCombo() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQueryCafe.deleteProduct_Cafe(this.data._id)
        .then((rsp : any) => {
          if(rsp.vcode === 0){
            if (this.comboForm.value["img"]) {
              this.vhImage.deleteThumbnail(this.comboForm.value["img"]).then(() => {
                this.vhComponent.showToast(2000, (this.languageService.translate("Product has been deleted successfully")), "success-toast");
                this.vhComponent.hideLoading(0);
                setTimeout(() => {
                  this.isVisible = false;
                  this.goBack();
                }, 300);
              })
            } else {
              this.vhComponent.hideLoading(0);
              this.vhComponent.alertMessageDesktop("success", (this.languageService.translate("Product has been deleted successfully")));
              setTimeout(() => {
                this.isVisible = false;
                this.goBack();
              }, 300);
            }
        }else if(rsp.vcode === 1){
          this.vhComponent.hideLoading(0);
          this.vhComponent.alertMessageDesktop("error", (this.languageService.translate("Bn khng th xa, v sn phm ang lin kt vi sn phm combo khc")));
          this.goBack();
        }else if(rsp.vcode === 2){
          this.vhComponent.hideLoading(0);
          this.vhComponent.alertMessageDesktop("error", (this.languageService.translate("Bn khng th xa, v sn phm ny  pht sinh ha n")));
          this.goBack();
        }
           
        });
    })
  }


  /**
   * cp nht sp trong combo
   * @example this.updateshow('DNSWIDHNBWIUDH', combo_product_selected) 
   */
  updateshow(_id, food) {
    console.log(food);
    
    return this.vhQueryCafe.updateComboProduct(_id, food._id, {
      price: food.price,
      ratio: food.ratio
    }).then((bool) => {
      console.log('bool', bool);
    }, error => {
      console.log('error', error);
    })
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/dishes/combo'], { state: this.dataRestore });
  }

  getFile() {
    document.getElementById("file-upload").click();
  }


  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 128, 128, "products", this.comboForm.value.img || null).then(
      photoURL => {
        this.comboForm.controls["img"].setValue(photoURL);
        this.updateCombo({img: this.comboForm.value.img});
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), "success-toast");
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), "alert-toast");
      }
    );
  }
    
    
    
  /** Nu l true th l ang thm danh mc, dng  hin th trng thi loading, trnh b nhn nhiu ln */
  isAddingCategory: boolean = false;
  /** Input nhp tn danh mc */
  @ViewChild('categoryName') categoryName;

  /**
   * Thm danh mc
   */
  addCategory(categoryName: any) {
    const value = categoryName.value ? categoryName.value?.toString()?.trim() : '';
    if (value) {
      // Kim tra xem danh mc  tn ti hay cha
      let index = this.vhQueryCafe.getlocalCategorys()
                    .filter(res => res.type == VhType.COMBO)
                    .findIndex(item => this.vhAlgorithm.changeAlias(item.name).toLowerCase() == this.vhAlgorithm.changeAlias(value).toLowerCase());

      //  tn ti
      if (index !== -1) {
        this.vhComponent.showToast(1500, this.languageService.translate("Category existed"), 'alert-toast');
        return;
      }

      // Cha tn ti th thm
      this.isAddingCategory = true;
      this.vhQueryCafe.addCategory({ name: value, type: VhType.COMBO }).then((res: any) => {
        console.log(res);
        this.categoryName.nativeElement.value = ''; // Reset input
        this.list_category.unshift(res); // Thm vo u danh sch
        this.comboForm.controls['id_category'].setValue(res._id); // Chn ci mi thm
        this.vhComponent.showToast(2000, (this.languageService.translate("Category has been added successfully")), 'success-toast');
      }, err => {
        console.log(err);
        this.vhComponent.showToast(1500, this.languageService.translate("An eror occured while adding category. Please try again"), 'alert-toast');
      }).finally(() => {
        this.isAddingCategory = false;
      });
    }
    else {
      this.vhComponent.showToast(1500, this.languageService.translate('Category name is required'), 'alert-toast');
    }
  }

}
